<script>
import Sourceshop from '@/components/Source/Sourceshop'
    export default{
        data(){
            return{
                sourcename:'',//
                list:'',//
                list01:'',//
                list02:'',//
                list03:'',//
                list04:'',//
                list05:'',//
                list06:'',//
                iscolor:'',//
                progressbg:'',//
                musicbg:'',//
                musicstart01:'',//
                musicend:'',//
                musicplace:'',//
                duration01:'',//()
                duration02:'',//()
                currentTime:'',//
                scale:0,//
                isduration:false,//
                isplay:[ ],//
                type:'',
                radio: 0,//
                listtotal:'',//
                listsortnum:[],//
                one:true,
                two:true,
                three:true,
                four:true,
                five:true,
                six:true,
            }
        },
        watch:{
            '$route':'islogin'
         },
        methods:{
            //
            collectmaster(id){
                    this.axios.post('/res/collectmaterial',{
                    id:id,
                })
                .then(response => {   

                })
            },
            //
            islogin(){
                if(this.$route.path==='/source/mysourceshop'){
                    if(this.$store.state.userid){
                        this.$router.push({ name: 'Myrole' })
                    }else{
                        this.$message({
                             message: '',
                             center: true,
                        })
                        this.$router.push({ name: 'Home' })
                    }
                }
            },
            //radio
            selectradio(val){
                switch(val){
                    case 0:
                    this.Getsearch()
                    break;
                    case 1:
                    this.one=true;
                    this.two=false;
                    this.three=false;
                    this.four=false;
                    this.five=false;
                    this.six=false;
                    if(this.listsortnum.includes("1")){
                        this.one=true
                    }else{
                        this.$message({
                            message:''
                        })
                    }
                    break;
                    case 2:
                    this.one=false;
                    this.two=true;
                    this.three=false;
                    this.four=false;
                    this.five=false;
                    this.six=false;
                    if(this.listsortnum.includes("2")){
                        this.two=true
                    }else{
                        this.$message({
                            message:''
                        })
                    }
                    break;
                    case 3:
                    this.one=false;
                    this.two=false;
                    this.three=true;
                    this.four=false;
                    this.five=false;
                    this.six=false;
                    if(this.listsortnum.includes("3")){
                        this.three=true
                    }else{
                        this.$message({
                            message:''
                        })
                    }
                    break;
                    case 4:
                    this.one=false;
                    this.two=false;
                    this.three=false;
                    this.four=true;
                    this.five=false;
                    this.six=false;
                    if(this.listsortnum.includes("4")){
                        this.four=true
                    }else{
                        this.$message({
                            message:''
                        })
                    }
                    break;
                    case 5:
                    this.one=false;
                    this.two=false;
                    this.three=false;
                    this.four=false;
                    this.five=true;
                    this.six=false;
                    if(this.listsortnum.includes("5")){
                        this.five=true
                    }else{
                        this.$message({
                            message:''
                        })
                    }
                    break;
                    case 6:
                    this.one=false;
                    this.two=false;
                    this.three=false;
                    this.four=false;
                    this.five=false;
                    this.six=true;
                    if(this.listsortnum.includes("6")){
                        this.six=true
                    }else{
                        this.$message({
                            message:''
                        })
                    }
                    break;
                }
            },
            //
            Getsearch(){
                this.$router.push({name:'sourceshop'})
                this.$store.state.sourcesearch=true,
                this.$store.state.sourcebackg=false,
                this.$store.state.sourcegame=false,
                this.$store.state.sourcegsource=false,
                this.$store.state.sourcemusic=false,
                this.$store.state.sourcesource01=false,
                this.$store.state.sourcelesson=false,
                this.$store.state.sourcestudy=false
                if(this.sourcename==''){
                    this.$message({
                            message: '',
                            center: true,
                    })
                }else{
                    this.axios.post('/res/resourcelist',{
                    searchname:this.sourcename,
                    pagesize:15
                })
                .then(response => {  
                    var searchresult = response.data.data.msg
                    if(searchresult=="~"){
                            this.list=response.data.data
                            this.list01=response.data.data
                            this.$message({
                            message: '',
                            center: true,
                    })
                    }else{
                        this.list=response.data.data
                        this.listtotal=response.data.total
                        let listsort = this.list
                        let sortnum01 = []//
                        let sortnum02 = []//
                        listsort.forEach(function(a){
                            sortnum01.push(a.onenav)
                        })
                        sortnum01.forEach(function(currentValue){
                            sortnum02.push(currentValue)
                        })
                        this.listsortnum = sortnum02
                        //
                        if(sortnum02.includes("1")){
                            this.one=true
                        }else{
                            this.one=false
                        }
                        if(sortnum02.includes("1")){
                            this.one=true
                        }else{
                            this.one=false
                        }
                        if(sortnum02.includes("2")){
                            this.two=true
                        }else{
                            this.two=false
                        }
                        if(sortnum02.includes("3")){
                            this.three=true
                        }else{
                            this.three=false
                        }
                        if(sortnum02.includes("4")){
                            this.four=true
                        }else{
                            this.four=false
                        }
                        if(sortnum02.includes("5")){
                            this.five=true
                        }else{
                            this.five=false
                        }
                        if(sortnum02.includes("6")){
                            this.six=true
                        }else{
                            this.six=false
                        }
                    }
                })
                }
            },
            //
            updateTime(j) {
                // this.currentTime = document.getElementById('bgmusic').currentTime
                // this.duration01 = document.getElementById('bgmusic').duration
                this.currentTime = this.$refs.audio[j].currentTime
                this.duration01 = this.$refs.audio[j].duration
                // console.log(this.currentTime)
                // console.log(this.duration01)
                this.scale=parseInt(this.currentTime/this.duration01*100)
            },
            //
            // time(index){
            //     var audiotime =  this.$refs.audio[index];
            //     this.duration02 = parseInt(audiotime.duration)
            // },
            //
            musicplay01(j){
                this.musicend=j
                this.musicstart01=-1
                this.progressbg=j
                this.progressbg=j
                this.isplay.push(j)
                // var audio01 = document.getElementById("bgmusic");
                // var audio01 = this.$refs.audio[j];
                if(this.isplay.length==1){
                    var audio01 = this.$refs.audio[j];
                    audio01.play();
                }else{
                    var audio001 = this.$refs.audio[j];
                    var beforej = this.isplay[this.isplay.length-2]
                    var audiobefore = this.$refs.audio[beforej];
                    audiobefore.pause();
                    audio001.play();
                }
                // var audio01 = this.$refs.audio[j];
                // let beforej = this.isplay[this.isplay.length-2]
                // var audiobefore = this.$refs.audio[beforej];
                // audiobefore.pause();
                // audio01.play();
                // console.log(beforej)
                // console.log(j)
                // console.log(audio01)
                // console.log(this.isplay)  
            },
            //
            musicstop(i){
                this.musicstart01=i
                this.musicend=-1
                this.progressbg=i
                this.isplay.push['i']
                // var audio02 = document.getElementById("bgmusic");
                var audio02 = this.$refs.audio[i];
                audio02.pause();
            },
            //
            musicstartbutton(i){
                this.progressbg=-1
                this.musicbg=i
                this.musicstart01=i
                this.musicend=-1
                this.isduration=i
                var audiotime = this.$refs.audio[i];
                this.duration02 = Math.ceil(audiotime.duration)
                let s = this.duration02%60
                let m = (this.duration02-s)/60
                if(s>9){
                    s=s
                }else{
                    s='0'+s
                }
                this.duration02=m+':'+s
            },
            //
            musicendbutton(i){
                this.progressbg=-1
                this.musicbg=-1
                this.musicstart01=-1
                this.musicend=-1
                this.isduration=-1
                // var audio03 = this.$refs.audio[i];
                // audio03.pause();
                this.scale=0;
            },
            //
            isbgcolor(i){
                if(i%5==1){
                    return this.iscolor='#f13232';
                }else if(i%5==2){
                    return this.iscolor='#0e74be';
                }else if(i%5==3){
                    return this.iscolor='#00BC59';
                }else if(i%5==4){
                    return this.iscolor='#FF9C00';
                }else{
                    return this.iscolor='#C711DD';
                }
            },
        },
        components:{
            Sourceshop
        }
    }
</script>
